
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'

import API from 'assets/js/api'
import Scroll from 'components/better-scroll/scroll'

export default {
name: "home",
data() {
return {
name: 'home',
val: '',
bannerList: [],





pullDownRefreshObj: false
}
},
methods: {
toast() {
this.$toast('Hi~')
},
alert() {
this.$alert('Hello~')
},
confirm() {
this.$confirm('Hi~')
},
showLoading() {
this.$showLoading('加载中...')
setTimeout(() => {
this.$hideLoading()
}, 2000)
},
getData() {
this.$get(API.bannerList)
.then(res => {
if (res.code === this.$CODE) {
this.bannerList = [...res.data.bannerList]
}
})
},
toFlatList() {
this.$router.push({
name: 'flatlist'
})
},
onPullingDown() {
setTimeout(() => {
this.$refs.scroll.forceUpdate()
}, 1000)

}
},
components: {
XButton,
XInput,
Swiper,
Scroll
}
}



